<template>
  <div class="tile" :style="tile.styles" @click="onTileClick" :class="{ empty: tile.isEmpty, locked: tile.isLocked }" />
</template>

<script>
export default {
  props: ['tile'],

  methods: {
    onTileClick () {
      this.$emit('clicked', this.tile)
    }
  }
}
</script>

<style lang="scss">
.tile {
  border: 1px solid #111;

  &.empty {
    box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 10px inset;

    &:hover {
      border-color: #111;
      cursor: default;
    }
  }

  &.locked {
    filter: grayscale(100%);
  }

  &:hover {
    border-color: #f00;
    cursor: pointer;
  }
}
</style>



